body {
  font-size: 16px;
  letter-spacing: 0.1rem;
  color: #251c1c;
}

p, h3 {
  margin: 0
}

h3 {
  text-transform: uppercase;
  padding-bottom: 0.5rem;
}

.app__icon_text_container {
  display: flex;
  margin-bottom: 0.5rem;
  text-align: justify;
}

.app__icon_text {
  line-height: 10px;
  padding-left: 5px;
}

.app__icon_text a {
  text-decoration: none;
  color: inherit;
}

.app__container {
  max-width: 960px;
  margin: 0 auto;
  position: relative;
  background-color: rgb(225, 220, 224, 0.3);
  box-shadow: 0 1rem 3rem rgb(0 0 0 / 15%);
}

.app__personal_info {
  padding: 2rem 1.5rem;
}

.app__carrier_summary,
.app__work_experience {
  padding: 0.5rem 1.5rem 0 1.5rem;
}

.app__personal_info {
  background-color: #CEAD6D;
  margin-bottom: 0.25rem;
}

.app__personal_info__name {
  font-size: 1.75rem;
  font-weight: 600;
}

.app__personal_info__position {
  font-size: 1.15rem;
}

.app__content {
  display: flex;
}

.app__content__left {
  flex-grow: 1;
}

.app__content__right {
  flex-grow: 4;
}

.app__box {
  margin-bottom: 0.25rem;
  margin-right: 0.25rem;
  background-color: #BACBDB;
  padding: 1rem 1.5rem;
}

.app__work_experience__article {
  margin-bottom: 0.5rem;
}

.app__work_experience__company {
  font-weight: 700;
  border-bottom: 2px solid #CEAD6D;
}

.app__work_experience__project {
  padding: 0.5rem 0 0.5rem 0.5rem;
}

.app__work_experience__project_role {
  font-weight: 700;
}

.app__work_experience__project_period {
  font-size: 0.75rem;
  font-style: italic;
}

.app__work_experience__project_area {
  margin: 0.25rem 0;
}

.app__work_experience__techs {
  display: flex
}

.app__work_experience__techs p {
  font-size: 0.75rem;
  background-color: #CEAD6D;
  padding: 0.25rem;
  margin-right: 0.25rem
}

.app__education {
  font-size: 0.85rem;
  margin-bottom: 0.5rem;
}

.app__education_field,
.app__skills_and_tools__header {
  font-weight: 700;
}

.app__languages {
  font-size: 0.85rem;
  margin-bottom: 0.15rem;
}

.app_language {
  font-weight: 600;
}

.app__education_period {
  font-style: italic;
}

.app__interests,
.app__skills_and_tools {
  font-size: 0.85rem;
}

.app__skills_and_tools div {
  margin-bottom: 0.5rem;
}

.app__skill_container {
  width: 100%;
  height: 10px;
  position: relative;
  margin: 3px 0 5px 0;
}

.app__skill_base {
  width: 100%;
  height: 10px;
  background-color: #464159;
  opacity: 0.5;
  position: absolute;
}

.app__skill_level {
  height: 10px;
  background-color: #464159;
  position: absolute;
}

.app__lang {
  padding: 0.5rem;
  display: flex;
  justify-content: flex-end;
}